import { Flex, Spacer, Button, Box } from '@chakra-ui/react';
import Logo from '../../asset/Logo';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { logout } from '../helper/userSlice';

const RegisterOrLogin = () => {
  const dispatchUser = useDispatch();
  const navigate = useNavigate();
  const token = useSelector(state => state.user.value.token);

  if (token != '') {
    return <Button onClick={() => dispatchUser(logout())}>Logout</Button>;
  } else {
    return (
      <>
        <Button mr="2rem" onClick={() => navigate('/register')}>
          Register
        </Button>
        <Button onClick={() => navigate('/login')}>Login</Button>
      </>
    );
  }
};

const HeaderFooterLay = ({ children }) => {
  return (
    <>
      <Flex px="2.75rem" py="2rem">
        <Box width="150px">
          <Logo />
        </Box>
        <Spacer />
        {RegisterOrLogin()}
      </Flex>
      {children}
    </>
  );
};

export default HeaderFooterLay;
