import { Grid, GridItem } from '@chakra-ui/react';
import HeaderFooterLay from './HeaderFooterLay';

const DashboardLay = ({ children }) => {
  return (
    <HeaderFooterLay>
      <Grid templateColumns={'repeat(5,1fr)'} gap={1}>
        <GridItem
          colStart={2}
          colEnd={5}
          p="1rem"
          borderRadius="lg"
          borderWidth="1px"
        >
          {children}
        </GridItem>
      </Grid>
    </HeaderFooterLay>
  );
};

export default DashboardLay;
